import React, { useState, useEffect, useContext } from "react";
import { Link, useNavigate } from "react-router-dom";
import Pagination from "react-js-pagination";
import axios from "axios";

import "../../css/bbslist.css";
import "../../css/page.css";
import { HttpHeadersContext } from "../context/HttpHeadersProvider";
import { AuthContext } from "../context/AuthProvider";

function BbsList() {
  const [bbsList, setBbsList] = useState([]);
  const { auth, setAuth } = useContext(AuthContext);
  const { headers, setHeaders } = useContext(HttpHeadersContext);
  const navigate = useNavigate();


  //  Hook
  const [choiceVal, setChoiceVal] = useState("");
  const [searchVal, setSearchVal] = useState("");

  // Paging
  const [page, setPage] = useState(1);
  const [pageSize, setPageSize] = useState(10);
  const [totalPages, setTotalPages] = useState(0);
  const [totalCnt, setTotalCnt] = useState(0);

  //   
  const getBbsList = async (page) => {
    try {
      const response = await axios.get("http://localhost:8080/board/list", {
        params: {"page": page - 1},
        headers: headers
      });

      setBbsList(response.data.content);
      setPageSize(response.data.pageSize);
      setTotalPages(response.data.totalPages);
      setTotalCnt(response.data.totalElements);
    } catch (error) {
      console.log("[BbsList.js] useEffect() error :<");
      console.log(error);
    }
  };

  //  
  const search = async () => {
    try {
      const response = await axios.get("http://localhost:8080/board/search", {
        params: {
          page: page - 1,
          title: choiceVal === "title" ? searchVal : "",
          content: choiceVal === "content" ? searchVal : "",
          writerName: choiceVal === "writer" ? searchVal : "",
        },
        headers: headers
      });

      console.log("[BbsList.js searchBtn()] success :D");
      console.log(response.data);

      setBbsList(response.data.content);
      setTotalCnt(response.data.totalElements);
    } catch (error) {
      console.log("[BbsList.js searchBtn()] error :<");
      console.log(error);
    }
  };

  //   ,   
  useEffect(() => {
    //    localStorage   headers 
    setHeaders({
      Authorization: `Bearer ${localStorage.getItem("bbs_access_token")}`,
    });

    //   
    if (!auth) {
      alert("       !");
      navigate(-1);
    }
  }, []);

  useEffect(() => {
    //    localStorage   headers 
    if(headers) {
      getBbsList(1);
    }

  }, [headers]);

  //   
  const changeChoice = (event) => { setChoiceVal(event.target.value);};
  const changeSearch = (event) => { setSearchVal(event.target.value);};

  //   
  const changePage = (page) => {
    setPage(page);
    getBbsList(page);
  };

  return (
      <div>
        {/*  */}
        <table className="search">
          <tbody>
          <tr>
            <td>
              <select
                  className="custom-select"
                  value={choiceVal}
                  onChange={changeChoice}
              >
                <option>  </option>
                <option value="title"></option>
                <option value="content"></option>
                <option value="writer"></option>
              </select>
            </td>
            <td>
              <input
                  type="text"
                  className="form-control"
                  placeholder=""
                  value={searchVal}
                  onChange={changeSearch}
              />
            </td>
            <td>
              <button
                  type="button"
                  className="btn btn-outline-secondary"
                  onClick={search}
              >
                <i className="fas fa-search"></i> 
              </button>
            </td>
          </tr>
          </tbody>
        </table>
        <br />

        <table className="table table-hover">
          <thead>
          <tr>
            <th className="col-1"></th>
            <th className="col-7"></th>
            <th className="col-3"></th>
            <th className="col-1"></th>
          </tr>
          </thead>

          <tbody>
          {bbsList.map(function (bbs, idx) {
            return <TableRow obj={bbs} key={idx} cnt={idx + 1} />;
          })}
          </tbody>
        </table>

        <Pagination
            className="pagination"
            activePage={page}
            itemsCountPerPage={pageSize}
            totalItemsCount={totalCnt}
            pageRangeDisplayed={totalPages}
            prevPageText={""}
            nextPageText={""}
            onChange={changePage}
        />

        <div className="my-5 d-flex justify-content-center">
          <Link className="btn btn-outline-secondary" to="/bbswrite">
            <i className="fas fa-pen"></i> &nbsp; 
          </Link>
        </div>
      </div>
  );
}

/*      */
function TableRow(props) {
  const bbs = props.obj;

  return (
      <tr>
        <th>{props.cnt}</th>
        <td>
          <Link to={{ pathname: `/bbsdetail/${bbs.boardId}` }}>
            <span className="underline bbs-title">{bbs.title}</span>
          </Link>
        </td>
        <td>{bbs.writerName}</td>
        <td style={{ textAlign: 'center' }}>{bbs.viewCount}</td>
      </tr>
  );
}

