import axios from "axios";
import { useContext, useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { AuthContext } from "../context/AuthProvider";
import { HttpHeadersContext } from "../context/HttpHeadersProvider";

import "../../css/bbswrite.css";

function BbsWrite() {
  const { auth, setAuth } = useContext(AuthContext);
  const { headers, setHeaders } = useContext(HttpHeadersContext);

  const navigate = useNavigate();

  const [title, setTitle] = useState("");
  const [content, setContent] = useState("");
  const [boardType, setBoardType] = useState("");
  const [files, setFiles] = useState([]); // :    

  const changeTitle = (event) => {
    setTitle(event.target.value);
  };

  const changeContent = (event) => {
    setContent(event.target.value);
  };

  const changeBoardType = (event) => {
    setBoardType(event.target.value);
  };

  const handleChangeFile = (event) => {
    //  5 
    const selectedFiles = Array.from(event.target.files).slice(0, 5);
    setFiles((prevFiles) => [...prevFiles, ...selectedFiles]);
  };

  const handleRemoveFile = (index) => {
    setFiles((prevFiles) => prevFiles.filter((_, i) => i !== index));
  };

  /*   */
  const fileUpload = async (boardId) => {
    //   
    const fd = new FormData();
    files.forEach((file) => fd.append("file", file));

    await axios
        .post(`http://localhost:8080/board/${boardId}/file/upload`, fd, { headers: headers })
        .then((resp) => {
          console.log("[file.js] fileUpload() success :D");
          console.log(resp.data);

          alert("   :D");
        })
        .catch((err) => {
          console.log("[FileData.js] fileUpload() error :<");
          console.log(err);
        });
  };

  /* [POST /bbs]:   */
  const createBbs = async () => {
    const req = {
      title: title,
      content: content,
      boardType: boardType,
    };

    await axios
        .post("http://localhost:8080/board/write", req, { headers: headers })
        .then((resp) => {
          console.log("[BbsWrite.js] createBbs() success :D");
          console.log(resp.data);
          const boardId = resp.data.boardId;
          console.log("boardId:", boardId);
          fileUpload(boardId);

          alert("    :D");
          navigate(`/bbsdetail/${resp.data.boardId}`); //     
        })
        .catch((err) => {
          console.log("[BbsWrite.js] createBbs() error :<");
          console.log(err);
        });
  };

  useEffect(() => {
    //    localStorage   headers 
    setHeaders({
      Authorization: `Bearer ${localStorage.getItem("bbs_access_token")}`,
    });

    //   
    if (!auth) {
      alert("       !");
      navigate(-1);
    }
  }, []);

  return (
      <div>
        <table className="table">
          <tbody>
          <tr>
            <th className="table-primary"></th>
            <td>
              <input type="text" className="form-control" value={localStorage.getItem("id")} size="50px" readOnly />
            </td>
          </tr>

          <tr>
            <th> </th>
            <td>
              <select value={boardType} onChange={changeBoardType}>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
              </select>
            </td>
          </tr>

          <tr>
            <th className="table-primary">{boardType === "" ? "":""}</th>
            <td>
              <input type="text" className="form-control" value={title} onChange={changeTitle} size="50px" />
            </td>
          </tr>

          <tr>
            <th className="table-primary">{boardType === "" ? "  ":""}</th>
            <td>
              <textarea className="form-control" value={content} onChange={changeContent} rows="10"></textarea>
            </td>
          </tr>
          <tr>
            <th className="table-primary"></th>
            <td>
              {files.map((file, index) => (
                  <div key={index} style={{ display: "flex", alignItems: "center" }}>
                    <p>
                      <strong>FileName:</strong> {file.name}
                    </p>
                    <button className="delete-button" type="button" onClick={() => handleRemoveFile(index)}>
                      x
                    </button>
                  </div>
              ))}
              {files.length < 5 && (
                  <div>
                    <input type="file" name="file" onChange={handleChangeFile} multiple="multiple" />
                  </div>
              )}
            </td>
          </tr>
          </tbody>
        </table>

        <div className="my-5 d-flex justify-content-center">
          <button className="btn btn-outline-secondary" onClick={createBbs}>
            <i className="fas fa-pen"></i> 
          </button>
        </div>
      </div>
  );
}

export default BbsWrite;